class screen extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isLoading: false,
            selectedNews: news[0],
            content: text.replace('{{title}}', news[0].title).replace('{{image}}', news[0].image),
            isFocusContent: false,
            isFocusList: false,
            activeTab: 0,
        }

        // this.checkType(props.location.pathname);
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({ isFocusContent: true })
        }, 1000);
        setTimeout(() => {
            this.setState({ isFocusList: true })
        }, 2000);
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.location.pathname != this.props.location.pathname) {
            // this.checkType(nextProps.location.pathname);
        }
    }

    checkType(index) {
        if (index == 0) {
            news[0].title = 'Tin dá»± Ã¡n';
        } else if(index == 1) {
            news[0].title = 'Tin doanh nghiá»‡p';
        } else{
            news[0].title = 'Tin thá»‹ trÆ°á»ng';
        }
        this.selectNews(news[0]);
    }

    selectNews(item) {
        this.setState({
            isLoading: true,
            selectedNews: item,
        });

        setTimeout(() => {
            this.setState({
                isLoading: false,
                content: text.replace('{{title}}', item.title)
                    .replace('{{image}}', item.image),
            });
        }, 1000);
    }

    clickMenu(index) {
        console.log('click index');
        this.setState({ activeTab: index })
        this.checkType(index);
    }

    render() {
        return [
            <div style={{
                overflow: 'hidden', height: '100vh',
                width: '100%',
                backgroundImage: `url(${require('../../assets/images/news-background.jpg')})`,
                backgroundSize: 'cover',
            }}>
                <div className="col-12 h-100 row mx-0 px-0" style={{
                    backgroundColor: 'rgba(0,0,0,0.4)'
                }}>
                    <NavigationHolder />
                    <SubNavHolder />
                    <SubNav
                        activeIndex={this.state.activeTab}
                        style={{ marginTop: '3.5em' }}
                        data={
                            (language.t('news.sub_menu') || [])
                                .map((item, index) => ({
                                    title: item,
                                    onClick: ()=>this.clickMenu(index),
                                }))
                        } />
                    <div className="col-4 h-100"
                        style={{ position: 'relative', overflow: 'hidden' }}
                    >
                        <div className=""
                            style={{
                                position: 'absolute',
                                width: '100%',
                                maxWidth: '17em',
                                height: '50vh',
                                bottom: '1em',
                                right: this.state.isFocusList ? '-1em' : '-20em',
                                paddingRight: '2em',
                                overflowY: 'scroll',
                                transition: 'all 2s',
                            }}>
                            {news.map(item =>
                                <div className={`w-100 row mx-0 my-2 news-item
                            ${this.state.selectedNews._id == item._id ? ' news-item-active' : ''} `}
                                    style={{
                                        cursor: 'pointer',
                                        height: '5em',
                                        backgroundColor: 'white',
                                        borderRadius: '20px 20px 0',
                                        overflow: 'hidden',
                                        position: 'relative',
                                    }}
                                    onClick={() => this.selectNews(item)}
                                >
                                    <img src={item.image}
                                        style={{
                                            width: '6em', height: '4em',
                                            margin: '0.5em',
                                            position: 'absolute',
                                            borderRadius: '8px',
                                        }}
                                    />
                                    <div style={{
                                        width: '100%',
                                        padding: '0.5em',
                                        paddingLeft: '8em',
                                        height: '100%',
                                        fontSize: '0.9em',
                                    }}>
                                        {item.title}
                                    </div>
                                </div>)}

                        </div>
                    </div>
                    <div
                        style={{ position: 'relative' }}
                        className="col-8 h-100">
                        <div className=""
                            style={{
                                position: 'absolute',
                                width: '100%',
                                height: '80vh',
                                bottom: this.state.isFocusContent ? '1em' : '-100vh',
                                right: '1em',
                                transition: '2s all',

                            }} >
                            <NewsBox>
                                {this.state.isLoading ?
                                    <div className="w-100 h-100 row mx-0 justify-content-center align-items-center">
                                        <ReactLoading type={'bars'} color={'#0B5C3B'} height={'10%'} width={'10%'} />
                                    </div> :
                                    <td dangerouslySetInnerHTML={{ __html: this.state.content }} />
                                }
                            </NewsBox>

                        </div>
                    </div>
                </div>

            </div>

        ]
    }
}


const mapStateToProps = state => ({
    app: state.app,
});

const mapDispatchToProps = {

}

export default connect(mapStateToProps, mapDispatchToProps)(screen);